import { Injectable } from '@angular/core';
import * as firebase from 'firebase';

import 'rxjs/add/operator/map';

@Injectable()
export class FirebaseProvider {

  constructor() {
  }
  //#region authentication
  // ==============================================================================================
  // firebase authentication

  signupMail(email: string, password: string) {  //input: email,password
    return new Promise((resolve, reject) => {
      firebase.auth().createUserWithEmailAndPassword(email,password).catch(function (error) {        //firebase authentication
        return (error.message);
      }).then((v) => {
        resolve(v);
      })
    })
  }

  signinMail(email: string, password: string) {  //input: email,password
    return new Promise((resolve, reject) => {
      firebase.auth().signInWithEmailAndPassword(email,password).catch(function (error) {
        return (error.message);
      }).then((v) => {
        resolve(v)
      })
    })
  }

  signin3rdparty(credential: any) { //input: credential
    return new Promise((resolve, reject) => {
      firebase.auth().signInWithCredential(credential).then((success) => {
        resolve(success)
      })
    })
  }

  signout() {
    return new Promise((resolve, reject) => {
      firebase.auth().signOut().then(function () { }).catch(function (error) {
        resolve(error);
      });
    })
  }

  // ============================================================================================
  // get firebase auth uid
  getProfile() {
    return new Promise((resolve, reject) => {
      var user: any = firebase.auth().currentUser;
      resolve(user);
    })
  }

  // ==============================================================================================
  // get verification key to email
  verificationMail() {
    firebase.auth().onAuthStateChanged(function (user) {
      user.sendEmailVerification();
    });
  }

  // =================================================================================================
  // check auth state
  authState() {
    return new Promise((resolve, reject) => {
      firebase.auth().onAuthStateChanged(function (user) {
        if (user) { resolve(true) }
        else { resolve(false) }
      });
    })
  }

  // ==================================================================================================
  // reset password "forgot password"
  resetPassword(email: string) {    //input: email
    return new Promise((resolve, reject) => {
      firebase.auth().sendPasswordResetEmail(email).then(function (d) {      //firebase verify your email
        resolve(d);
      }, function (error) {
        resolve(error.message)
      })
    })
  }


  // ==================================================================================================
  // changing email
  updateEmail(password: string) {    //input: password
    return new Promise((resolve, reject) => {
      firebase.auth().currentUser.updateEmail(password);
      resolve("success");
    })
  }

  // ==================================================================================================
  // changing password
  updatePassword(password: string) {   //input: password
    return new Promise((resolve, reject) => {
      firebase.auth().currentUser.updatePassword(password);
      resolve("success");
    })
  }
  //#endregion

  //#region get data from firebase
  // ========================================================================================
  // get data from firebase
  // input (link, orderBy, {query key: query value})

  getData(ref: string, orderBy?: string, data?: firebaseQuery) {
    var order = (orderBy != (undefined)) ? "orderBy" + orderBy.charAt(0).toUpperCase() + orderBy.slice(1) : "orderByKey";
    order.replace(/ /g, '');
    var obj = (data == null) ? {} : data;
    var keys = Object.keys(obj);
    return new Promise((resolve, reject) => {
      try {
        var x;
        if (order.substring(0, 12) === 'orderByChild') {
          x = firebase.database().ref(ref)[order.split(':')[0]](order.split(':')[1])
        } else {
          x = firebase.database().ref(ref)[order]();
        }
        for (let i = 0; i < keys.length; i++) {
          x = x[keys[i]](obj[keys[i]]);
        }
        x.once('value').then(
          function (snapshot) { return snapshot.val(); }).then((snapshot) => { resolve(snapshot); })
      } catch (e) {
        reject(e);
        firebase.database().goOffline();
        setTimeout(() => { firebase.database().goOnline(); }, 2000)
      }
    })
  }

  // ========================================================================================
  // get data from firebase as array
  // input (link, orderBy, {query key: query value})

  getDataArr(ref: string, orderBy?: string, data?: firebaseQuery) {
    var order = (orderBy != (undefined)) ? "orderBy" + orderBy.charAt(0).toUpperCase() + orderBy.slice(1) : "orderByKey";
    order.replace(/ /g, '');
    var array = [];
    var obj = (data == null) ? {} : data;
    var keys = Object.keys(obj);
    return new Promise((resolve, reject) => {
      try {
        var x;
        if (order.substring(0, 12) === 'orderByChild') {
          x = firebase.database().ref(ref)[order.split(':')[0]](order.split(':')[1])
        } else {
          x = firebase.database().ref(ref)[order]();
        }
        for (let i = 0; i < keys.length; i++) {
          x = x[keys[i]](obj[keys[i]]);
        }
        x.once('value').then(function (snapshot) {
          snapshot.forEach(childSnapshot => { let item = childSnapshot.val(); array.push(item); });
          resolve(array);
        })
      }
      catch (e) {
        reject(e);
        firebase.database().goOffline();
        setTimeout(() => { firebase.database().goOnline(); }, 2000)
      }
    })
  }


  // ===================================================================================================
  // listen to database

  startListen(ref: string, callback: (snapshot: any) => void, orderBy?: string, data?: firebaseQuery) {
    var order = (orderBy != (undefined)) ? "orderBy" + orderBy.charAt(0).toUpperCase() + orderBy.slice(1) : "orderByKey";
    order.replace(/ /g, '');
    var obj = (data == null) ? {} : data;
    var keys = Object.keys(obj);
    try {
      var x;
      if (order.substring(0, 12) === 'orderByChild') {
        x = firebase.database().ref(ref)[order.split(':')[0]](order.split(':')[1])
      } else {
        x = firebase.database().ref(ref)[order]();
      }
      for (let i = 0; i < keys.length; i++) {
        x = x[keys[i]](obj[keys[i]]);
      }
      x.on('value',
        function (snapshot) { return new Promise((resolve, reject) => { resolve(snapshot.val()) }).then(callback) });
    } catch (e) {
      firebase.database().goOffline();
      setTimeout(() => { firebase.database().goOnline(); }, 2000)
    }
  }


  // ===================================================================================================
  // listen to database as array

  startListenArr(ref: string, callback: (snapshot: any) => void, orderBy?: string, data?: firebaseQuery) {
    var order = (orderBy != (undefined)) ? "orderBy" + orderBy.charAt(0).toUpperCase() + orderBy.slice(1) : "orderByKey";
    order.replace(/ /g, '');
    var array;
    var obj = (data == null) ? {} : data;
    var keys = Object.keys(obj);
    return new Promise((resolve, reject) => {
      try {
        var x;
        if (order.substring(0, 12) === 'orderByChild') {
          x = firebase.database().ref(ref)[order.split(':')[0]](order.split(':')[1])
        } else {
          x = firebase.database().ref(ref)[order]();
        }
        for (let i = 0; i < keys.length; i++) {
          x = x[keys[i]](obj[keys[i]]);
        }
        x.on('value',
          function (snapshot) {
            return new Promise((resolve, reject) => {
              array = [];
              snapshot.forEach(childSnapshot => { let item = childSnapshot.val(); array.push(item); });
              resolve(array);
            }).then(callback)
          })
      } catch (e) {
        reject(e);
        firebase.database().goOffline();
        setTimeout(() => { firebase.database().goOnline(); }, 2000)
      }
    })
  }

  // ===================================================================================================
  // stop Listen to firebase data
  stopListen(ref: string) {    //input: firebase path
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).off();
      resolve("success");
    })
  }

  //#endregion

  //#region set data to firebase

  // ==================================================================
  // set data to firebase

  setData(ref: string, data: any) { //input: firebase path, data
    return new Promise((resolve, reject) => {
      try {
        firebase.database().ref(ref).set(data).then(() => {
          resolve("success")
        })
      } catch (e) {
        reject(e);
      }
    })
  }


  // ==================================================================
  // push data to firebase

  pushData(ref: string, data: any) { //input: firebase path,data
    return new Promise((resolve, reject) => {
      var myRef = firebase.database().ref(ref).push();
      myRef.set(data);
      resolve(myRef.key);
    })
  }
  // endregion


  // ======================================================================================================
  // move firebase record from path to another


  moveFbRecord(x: string, y: string) {  //input: move from x path to y path
    firebase.database().ref(x).once('value').then((snap) => {
      firebase.database().ref(y).set(snap.val(), function (error) {
        if (!error) { firebase.database().ref(x).remove(); }
        else if (typeof (console) !== 'undefined' && console.error) { console.error(error); }
      });
    });
  }
  // ======================================================================================================
  // move firebase record from path to another


  copyFbRecord(x: string, y: string) {  //input: move from x path to y path
    firebase.database().ref(x).once('value').then((snap) => {
      firebase.database().ref(y).set(snap.val(), function (error) {
      });
    });
  }



  // =====================================================================================================
  // increment firebase record without read it
  increase(ref: string) { //input: firebase path
    firebase.database().ref(ref).transaction(function (currentClicks) {
      return (currentClicks || 0) + 1;
    });
  }
  // =====================================================================================================
  // decrement firebase record without read it
  decrease(ref: string) {      //input: firebase path
    firebase.database().ref(ref).transaction(function (currentClicks) {
      return (currentClicks) - 1;
    });
  }

  // ============================================================================
  // delete path in firebase

  deleteData(ref: string) { //input: firebase path
    return new Promise((resolve, reject) => {
      var myRef = firebase.database().ref(ref).remove();
    })
  }


  // ==================================================================
  // check is data available
  checkExist(ref: string) {    //input: firebase path
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).once('value').then(function (snapshot) {
        return snapshot.val();
      }).then((snapshot) => {
        if (snapshot != undefined) { resolve("true"); }
        else { resolve("false") }
      })
    })
  }

  // ==================================================================
  // upload string like base64

  uploadString(encodedFile: string, filePath: string, fileType: string, callback?: (snapshot: any) => void) {
    return new Promise((resolve, reject) => {
      let storageRef = firebase.storage().ref();
      const imageRef = storageRef.child(filePath);
      if (callback == undefined) { callback = (snapshot) => { } }
      imageRef.putString(encodedFile, fileType).on(firebase.storage.TaskEvent.STATE_CHANGED, callback,
        (e) => {
          reject(JSON.stringify(e, null, 2));
        }, () => {
          imageRef.getDownloadURL().then(function (url) {
            resolve(url)
          });
        })
    })
  }
  // ==================================================================
  // upload file to database


  uploadFile(File: any, filePath: string, callback?: (snapshot: any) => void) {
    return new Promise((resolve, reject) => {
      let storageRef = firebase.storage().ref();
      const imageRef = storageRef.child(filePath);
      if (callback == undefined) { callback = (snapshot) => { } }
      imageRef.put(File).on('state_changed', callback,
        (e) => {
          reject(JSON.stringify(e, null, 2));
        }, () => {
          imageRef.getDownloadURL().then(function (url) {
            resolve(url)
          });
        });
    })
  }

  // ==================================================================
  // get linkurl from file path 

  getFileLink(filePath: string) {
    return new Promise((resolve, reject) => {
      let storageRef = firebase.storage().ref().child(filePath);
      storageRef.getDownloadURL().then(function (url) {
        resolve(url)
      });
    })
  }

  // ==================================================================
  // remove file to database

  removeFile(filePath: string, successfulCallback?: () => void, failCallback?: (error: any) => void) {
    firebase.storage().ref().child(filePath).delete().then(successfulCallback).catch(failCallback);
  }

}


export class firebaseQuery {
  startAt?: any;
  endAt?: any;
  equalTo?: any;
  limitToFirst?: number;
  limitToLast?: number;
}